<template>
  <el-dialog
    :title="$t('IDC.till.editMSISDN')"
    :visible="showEditMsisdneDialog"
    :width="showMsisdnRange ? '845px' : '448px'"
    class="dialog-container"
    :close-on-click-modal="false"
    @close="closeEditLanguageDialog()"
  >
    <div class="dialog">
      <div>
        <el-form
          ref="editMsisdnFormRef"
          :model="editMsisdnForm"
          :rules="editLanguageFormRules"
          label-position="top"
          label-width="200px"
        >
          <el-form-item prop="currentMSISDN" :label="$t('IDC.till.currentMSISDN')">
            <div class="history-item">{{ editMsisdnForm.currentMSISDN }}</div>
          </el-form-item>
          <el-form-item class="from-item-title" prop="newMsisdn" :label="$t('IDC.till.newMSISDN')">
            <span type="primary" class="view-change" :underline="false" @click="viewMsisdnRange"
              >{{ $t('IDC.till.viewRange') }}
              <svg-icon
                v-if="!showMsisdnRange"
                icon-class="double-arrow-right"
                class="icon-double-arrow"
                @click="viewMsisdnRange"
              ></svg-icon>
              <svg-icon
                v-else
                icon-class="double-arrow-left"
                class="icon-double-arrow"
                @click="viewMsisdnRange"
              ></svg-icon
            ></span>
            <el-input
              id="newMsisdn"
              prop="newMsisdn"
              v-model="editMsisdnForm.newMsisdn"
              :placeholder="$t('common.placeholder.enter')"
            >
            </el-input>
          </el-form-item>
          <el-form-item class="from-item-title" prop="remark" :label="$t('IDC.till.remark')">
            <el-input
              id="editLanguageFormRemark"
              v-model.trim="editMsisdnForm.remark"
              clearable
              type="textarea"
              :rows="4"
              resize="none"
              :placeholder="$t('common.placeholder.enter')"
            >
            </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button id="cancel" @click="closeEditLanguageDialog()" size="small">{{
            $t('common.button.cancel')
          }}</el-button>
          <el-button
            :loading="submitLoading"
            id="confirm"
            type="primary"
            @click="submitDialogForm()"
            size="small"
            v-preventReClick
            >{{ $t('common.button.submit') }}</el-button
          >
        </div>
      </div>
      <div class="dialog-item" v-if="showMsisdnRange">
        <div style="margin-top: 24px; width: 348px">
          <span class="range-title">Till MSISDN Range</span>
          <table-page
            style="margin-top: 20px; overflow: auto"
            height="270"
            class="till-tab"
            :outSideTableData="msisdnRangeLists"
            :hasPagination="false"
            :columns="columns"
          >
          </table-page>
        </div>
      </div>
    </div>
  </el-dialog>
</template>
<script>
import TablePage from '@/components/tablePage';
import { loadRangeDcc, ediMsisdn } from '@/views/identifier-center/api/api-till';
import { getDetailMsisdnRules } from '@/views/identifier-center/validator/till.js';
export default {
  name: 'editMsisdn',
  components: { TablePage },
  data() {
    return {
      submitLoading: false,
      labelData: {},
      showMsisdnRange: false,
      msisdnRangeLists: [],
      showEditMsisdneDialog: false,
      editMsisdnForm: {
        currentMSISDN: '',
        newMsisdn: '',
        remark: '',
      },
      editLanguageFormRules: getDetailMsisdnRules(),
      columns: [
        { width: '85px', label: this.$t('IDC.till.no') + '.', prop: 'index', align: 'left' },
        { label: this.$t('IDC.till.range'), prop: 'range', align: 'left' },
      ],
      newLanguageOptions: [],
      languages: [],
    };
  },
  mounted() {},

  methods: {
    initMsisdnRange() {
      if (this.msisdnRangeLists != null && this.msisdnRangeLists.length > 0) {
        return;
      }
      loadRangeDcc({}).then((res) => {
        if (res.missdnRanges && res.missdnRanges.length) {
          res.missdnRanges.forEach((item, index) => {
            this.msisdnRangeLists.push({
              range: item.numberBegin + ' ~ ' + item.numberEnd,
              index: index + 1,
            });
          });
        }
      });
    },
    submitDialogForm() {
      this.$refs['editMsisdnFormRef'].validate((valid) => {
        if (valid) {
          this.submitLoading = true;
          let params = {
            receiverIdentityId: this.labelData.identityId,
            msisdn: this.editMsisdnForm.newMsisdn,
            remark: this.editMsisdnForm.remark,
          };
          ediMsisdn(params).then((res) => {
            this.submitLoading = false;
            if (res.header && res.header.success) {
              this.editSuccess();
            } else {
              this.showEditMsisdneDialog = false;
            }
          });
        }
      });
    },
    editSuccess() {
      this.$emit('closeDialog', { type: 'msisdn', value: '' });
      this.showEditMsisdneDialog = false;
      this.$modal.msgSuccess(this.$t('IDC.till.editMSISDNY'));
      this.$refs['editMsisdnFormRef'].resetFields();
    },
    closeEditLanguageDialog() {
      this.showEditMsisdneDialog = false;
      this.$refs['editMsisdnFormRef'].resetFields();
      this.showMsisdnRange = false;
    },
    handleEditLanguage() {
      this.showEditMsisdneDialog = true;
    },
    showView(val) {
      this.showEditMsisdneDialog = true;
      this.labelData = val;
      this.editMsisdnForm.currentMSISDN = val.devices[0].msisdn;
      this.initMsisdnRange();
    },
    viewMsisdnRange() {
      this.showMsisdnRange = !this.showMsisdnRange;
    },
  },
};
</script>
