<template>
  <el-dialog
    :title="$t('IDC.till.editDefaultOperatorId')"
    :visible="showOperatorIdDialog"
    width="448px"
    class="dialog-container"
    :close-on-click-modal="false"
    @close="closeEditLanguageDialog()"
  >
    <el-form
      ref="eeditOperatorIdDialog"
      :model="editOperatorId"
      :rules="editLanguageFormRules"
      label-position="top"
      label-width="200px"
    >
      <el-form-item prop="currentOperatorId" :label="$t('IDC.till.currentOperatorId')">
        <div class="history-item">{{ editOperatorId.currentOperatorId }}</div>
      </el-form-item>
      <el-form-item
        :rules="defaultOperatorIDRules"
        prop="newOperatorId"
        class="from-item-title"
        :label="$t('IDC.till.newOperatorId')"
      >
        <el-select
          id="newOperatorId"
          @change="selectChange"
          v-model="editOperatorId.newOperatorId"
          :placeholder="$t('common.placeholder.select')"
          size="small"
        >
          <el-option v-for="item in newOperatorIdList" :key="item.value" :label="item.label" :value="item"> </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="remark" class="from-item-title" :label="$t('IDC.till.remark')">
        <el-input
          id="editLanguageFormRemark"
          v-model.trim="editOperatorId.remark"
          clearable
          type="textarea"
          :rows="4"
          resize="none"
          :placeholder="$t('common.placeholder.enter')"
        >
        </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button id="cancel" @click="closeEditLanguageDialog()" size="small" style="width: 82px">{{
        $t('common.button.cancel')
      }}</el-button>
      <el-button
        :loading="submitLoading"
        id="confirm"
        type="primary"
        @click="submitDialogForm()"
        size="small"
        style="width: 96px"
        v-preventReClick
        >{{ $t('common.button.submit') }}</el-button
      >
    </div>
  </el-dialog>
</template>
<script>
import { editOperatorId, queryOrgOperatorIdList } from '@/views/identifier-center/api/api-till';
import { inputRequiredValidateObj } from '@/common/validator/common';
import { getDefaultOperatorIDRules } from '@/views/identifier-center/validator/till.js';
export default {
  name: 'editDefaultOperator',
  data() {
    return {
      submitLoading: false,
      showOperatorIdDialog: false,
      editOperatorId: {
        currentOperatorId: '',
        newOperatorId: '',
        remark: '',
      },
      newOperatorIdList: [],
      editLanguageFormRules: {
        remark: [inputRequiredValidateObj],
      },
      labelData: {},
      defaultOperatorIDRules: [],
    };
  },
  mounted() {},
  methods: {
    selectChange(val) {
      if (val.value === '') {
        this.editOperatorId.newOperatorId = '';
      }
      this.$refs['eeditOperatorIdDialog'].validateField('newOperatorId');
    },
    async getIdrulues() {
      this.defaultOperatorIDRules = await getDefaultOperatorIDRules();
    },
    async getSelectList() {
      let params = {
        identityId: this.labelData.organizationId,
      };
      this.newOperatorIdList = [];
      let res = await queryOrgOperatorIdList(params);
      if (res.header && res.header.success) {
        let list = [];
        if (res.orgOperatorList && res.orgOperatorList.length) {
          res.orgOperatorList.forEach((item) => {
            list.push({
              label: item.operatorId,
              value: item.orgOperatorId,
            });
          });
        }
        this.newOperatorIdList = list;
      }
      this.newOperatorIdList.unshift({
        label: 'Please select',
        value: '',
      });
    },
    closeEditLanguageDialog() {
      this.showOperatorIdDialog = false;
      this.$refs['eeditOperatorIdDialog'].resetFields();
    },
    handleEditLanguage() {
      this.showOperatorIdDialog = true;
    },
    showLanguageName() {},
    showView(val) {
      this.showOperatorIdDialog = true;
      this.labelData = val;
      this.getSelectList();
      this.editOperatorId.currentOperatorId = val.defaultOperatorIdentifier;
      if (val.defaultOperatorId && val.defaultOperatorIdentifier) {
        this.editOperatorId.newOperatorId = {
          value: val.defaultOperatorId,
          label: val.defaultOperatorIdentifier,
        };
      } else {
        this.editOperatorId.newOperatorId = '';
      }
      this.getIdrulues();
    },
    submitDialogForm() {
      this.$refs['eeditOperatorIdDialog'].validate((valid) => {
        if (valid) {
          this.submitLoading = true;
          let params = {
            receiverIdentityId: this.labelData.identityId,
            defaultOperatorId: this.editOperatorId.newOperatorId.value || '',
            remark: this.editOperatorId.remark,
          };
          editOperatorId(params).then((res) => {
            this.submitLoading = false;
            if (res.header && res.header.success) {
              this.editSuccess();
            } else {
              this.showOperatorIdDialog = false;
            }
          });
        }
      });
    },
    editSuccess() {
      let label = this.editOperatorId.newOperatorId.label ? this.editOperatorId.newOperatorId.label : '';
      let value = this.editOperatorId.newOperatorId.label ? this.editOperatorId.newOperatorId.value : '';
      this.$emit('closeDialog', {
        type: 'operatorId',
        value: {
          label: label,
          value: value,
        },
      });
      this.showOperatorIdDialog = false;
      this.$modal.msgSuccess(this.$t('IDC.till.editIdY'));
      this.$refs['eeditOperatorIdDialog'].resetFields();
    },
  },
};
</script>
