<template>
  <el-dialog
    :title="$t('IDC.till.editLanguage')"
    :visible="showEditLanguageDialog"
    width="448px"
    class="dialog-container"
    :close-on-click-modal="false"
    @close="closeEditLanguageDialog()"
  >
    <el-form
      ref="editLanguageFormRef"
      :model="editLanguageForm"
      :rules="editLanguageFormRules"
      label-position="top"
      label-width="200px"
    >
      <el-form-item prop="oldLanguage" :label="$t('IDC.till.oldLanguage')">
        <div class="history-item">{{ this.formatLanguage(editLanguageForm.oldLanguage) }}</div>
      </el-form-item>
      <el-form-item class="from-item-title" prop="newLanguage" :label="$t('IDC.till.newLanguage')">
        <el-select
          id="newLanguage"
          v-model="editLanguageForm.newLanguage"
          :placeholder="$t('common.placeholder.select')"
          size="small"
        >
          <el-option
            v-for="item in newLanguageOptions"
            :key="item.languageCode"
            :label="item.languageName"
            :value="item.languageCode"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="from-item-title" prop="remark" :label="$t('IDC.till.remark')">
        <el-input
          id="editLanguageFormRemark"
          v-model.trim="editLanguageForm.remark"
          clearable
          type="textarea"
          :rows="4"
          resize="none"
          :placeholder="$t('common.placeholder.enter')"
        >
        </el-input>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button id="cancel" @click="closeEditLanguageDialog()" size="small" style="width: 82px">{{
        $t('common.button.cancel')
      }}</el-button>
      <el-button
        :loading="submitLoading"
        id="confirm"
        type="primary"
        @click="submitDialogForm()"
        size="small"
        style="width: 96px"
        v-preventReClick
        >{{ $t('common.button.submit') }}</el-button
      >
    </div>
  </el-dialog>
</template>
<script>
import { editLanguage, getLanguages } from '@/views/identifier-center/api/api-till';
import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';
export default {
  name: 'editLanguage',
  data() {
    return {
      submitLoading: false,
      showEditLanguageDialog: false,
      editLanguageForm: {
        oldLanguage: '',
        newLanguage: '',
        remark: '',
      },
      editLanguageFormRules: {
        newLanguage: [selectRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      newLanguageOptions: [],
      languages: [],
      labelData: {},
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},

  methods: {
    async getLanguagesList() {
      let params = {
        dataId: 'CACHE_LANGUAGE',
        groupId: 'AG',
      };
      let res = await getLanguages(params);
      if (res.header && res.header.success) {
        this.newLanguageOptions = res.list;
      }
    },
    closeEditLanguageDialog() {
      this.showEditLanguageDialog = false;
      this.$refs['editLanguageFormRef'].resetFields();
    },
    handleEditLanguage() {
      this.showEditLanguageDialog = true;
    },
    showLanguageName() {},
    showView(val) {
      this.getLanguagesList();
      this.showEditLanguageDialog = true;
      this.labelData = val;
      this.editLanguageForm.oldLanguage = val.languageCode;
      this.editLanguageForm.newLanguage = val.languageCode;
    },
    submitDialogForm() {
      this.$refs['editLanguageFormRef'].validate((valid) => {
        if (valid) {
          this.submitLoading = true;
          let params = {
            receiverIdentityId: this.labelData.identityId,
            languageCode: this.editLanguageForm.newLanguage,
            remark: this.editLanguageForm.remark,
          };
          editLanguage(params).then((res) => {
            if (res.header && res.header.success) {
              this.editSuccess();
            } else {
              this.showEditLanguageDialog = false;
            }
            this.submitLoading = false;
          });
        }
      });
    },
    formatLanguage(val) {
      let languageName = '-';
      this.newLanguageOptions.forEach((item) => {
        if (val === item.languageCode) {
          languageName = item.languageName;
        }
      });
      return languageName;
    },
    editSuccess() {
      this.$emit('closeDialog', { type: 'language', value: this.editLanguageForm.newLanguage });
      this.showEditLanguageDialog = false;
      this.$modal.msgSuccess(this.$t('IDC.till.editLanguageY'));
      this.$refs['editLanguageFormRef'].resetFields();
    },
  },
};
</script>
