<template>
  <div class="customer-info-container">
    <div>
      <div class="till-title">
        <div class="title">Till - {{ labelData.tillNumber }}</div>
        <span class="status-label">{{ $t('common.label.status') + ':' }}</span>
        <span class="edit-status">
          {{ that.showStatus(labelData.status) }}
          <i
            v-if="labelData.status == '03' || labelData.status == '05'"
            id="language"
            class="el-icon-black-edit icon-cursor margin-left-8"
            @click="handleEditStatus(labelData)"
          />
        </span>
      </div>
      <el-tabs v-model="activeName" @tab-click="tabChange" class="tabs">
        <el-tab-pane :label="$t('IDC.till.info')" name="info">
          <div class="basic-info">
            <div class="basic-info-title">
              <span>{{ $t('IDC.till.basicInfo') }}</span>
            </div>
            <div class="info-style">
              <div class="basic-info-content">
                <el-row :gutter="20">
                  <el-col class="el-col-org-adaption">
                    <p>{{ $t('IDC.till.tillNumber') }}</p>
                    <p>{{ labelData.tillNumber | formatEmpty }}</p>
                  </el-col>
                  <el-col class="el-col-org-adaption">
                    <p>{{ $t('IDC.till.organizationShortCode') }}</p>
                    <p>{{ labelData.shortCode | formatEmpty }}</p>
                  </el-col>
                  <el-col class="el-col-org-adaption">
                    <p>{{ $t('IDC.till.organizationName') }}</p>
                    <p>{{ labelData.organizationName | formatEmpty }}</p>
                  </el-col>
                  <el-col class="el-col-org-adaption">
                    <p>{{ $t('IDC.till.language') }}</p>
                    <p>
                      {{ this.formatLanguage(labelData.languageCode) }}
                      <i
                        id="language"
                        class="el-icon-black-edit icon-cursor margin-left-8"
                        @click="handleEditLanguage(labelData)"
                      />
                    </p>
                  </el-col>
                  <el-col class="el-col-org-adaption">
                    <p>{{ $t('IDC.till.defaultOperatorId') }}</p>
                    <p>
                      {{ labelData.defaultOperatorIdentifier | formatEmpty }}
                      <i
                        id="language"
                        class="el-icon-black-edit icon-cursor margin-left-8"
                        @click="handleEditDefaultOperator(labelData)"
                      />
                    </p>
                  </el-col>
                  <el-col class="el-col-org-adaption">
                    <p>{{ $t('IDC.till.registrationDate') }}</p>
                    <p>{{ labelData.createTime | localTimeFormat('DD-MM-YYYY HH:mm:ss') }}</p>
                  </el-col>
                </el-row>
              </div>
            </div>
            <div class="detailInfo el-des">
              <el-tabs v-model="tabData" @tab-click="handleClick">
                <el-tab-pane :label="$t('IDC.till.device')" name="first">
                  <div class="sub-title">
                    {{ $t('IDC.till.labelTitle') }}
                  </div>
                  <table-page
                    class="till-tab"
                    :outSideTableData="labelData.devices"
                    :hasPagination="false"
                    :sortArr="['status']"
                    :columns="columns"
                  >
                    <template slot="msisdn" slot-scope="scope">
                      {{ scope.data.row.msisdn }}
                      <i
                        id="language"
                        class="el-icon-black-edit icon-cursor margin-left-8"
                        @click="handleEditMsisdn(labelData)"
                      />
                    </template>
                    <template slot-scope="scope" slot="status">
                      <span style="margin-right: 4px" :class="'status status' + scope.data.row.status"></span>
                      <span>{{ showStatus(scope.data.row.status) }}</span>
                    </template>
                  </table-page>
                  <div class="historyDevice">
                    <span>{{ $t('IDC.till.historyDevice') }}</span>
                  </div>
                  <table-page
                    class="till-tab histor-device-tab"
                    :hasPagination="false"
                    :outSideTableData="labelData.deviceHisList"
                    :columns="historyDevice"
                    :sortArr="['status', 'lastUpdateTime']"
                  >
                    <template slot-scope="scope" slot="status">
                      <span style="margin-right: 4px" :class="'status status' + scope.data.row.status"></span>
                      <span>{{ showStatus(scope.data.row.status) }}</span>
                    </template>
                    <template slot-scope="scope" slot="lastUpdateTime">
                      <span>{{ scope.data.row.lastUpdateTime | localTimeFormat('DD-MM-YYYY HH:mm:ss') }}</span>
                    </template></table-page
                  >
                </el-tab-pane>
                <el-tab-pane :label="$t('IDC.till.product')" name="second">
                  <div class="sub-title">
                    {{ $t('IDC.till.tabTitle') }}
                  </div>
                  <el-button
                    @click="showProduct"
                    v-if="isShowProduct"
                    size="small"
                    class="till-tab"
                    :class="labelData.status === '06' ? 'disabled-btn' : 'editBtn'"
                    icon="el-icon-edit"
                    >{{ $t('common.button.edit') }}</el-button
                  >
                  <el-button
                    v-if="!isShowProduct"
                    @click="addProduct"
                    size="small"
                    class="editBtn till-tab"
                    icon="el-icon-plus"
                    >{{ $t('common.button.add') }}</el-button
                  >

                  <table-page
                    :outSideTableData="showTillProducts"
                    class="till-tab"
                    :class="isShowProduct ? 'till-tab-bottom' : ''"
                    :hasPagination="false"
                    :columns="productColumns"
                    :sortArr="['productName', 'description']"
                  >
                    <template slot-scope="scope" slot="operation">
                      <el-button size="mini" type="text" @click="deleteTill(scope.data.$index)">
                        {{ $t('common.button.delete') }}
                      </el-button>
                    </template></table-page
                  >
                  <el-form
                    ref="productForm"
                    :model="productDataForm"
                    :rules="tillFormRules"
                    v-if="!isShowProduct"
                    class="till-bottom"
                  >
                    <el-form-item prop="remark" label="Remark">
                      <el-input v-model="productDataForm.remark" id="editLanguageFormRemark" clearable type="textarea">
                      </el-input> </el-form-item
                  ></el-form>
                </el-tab-pane>
                <div v-if="!isShowProduct">
                  <div class="margin-right-20 produce-sumit" style="float: right">
                    <el-button size="small" type="plain" id="cancel" class="cancel-button" @click="cancel()">{{
                      $t('common.button.cancel')
                    }}</el-button>
                    <el-button
                      :loading="editProductLogding"
                      id="submitCreateTaskjob"
                      size="small"
                      type="primary"
                      @click="changeProduct"
                      v-preventReClick
                    >
                      {{ $t('common.button.submit') }}
                    </el-button>
                  </div>
                </div>
              </el-tabs>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane :lazy="true" :label="$t('IDC.till.auditLog')" name="auditLog">
          <audit-log :title="tillAudioLog" :initiatorType="'Till'" :identityId="tillId"></audit-log>
        </el-tab-pane>
      </el-tabs>
    </div>
    <edit-language ref="editLanguage" @closeDialog="closeDialog"></edit-language>
    <edit-status ref="editStatus" @closeDialog="closeDialog"></edit-status>
    <edit-msisdn ref="editMsisdn" @closeDialog="closeDialog"></edit-msisdn>
    <edit-operatorId ref="editDefaultOperator" @closeDialog="closeDialog"></edit-operatorId>
    <portal-add-popup
      id="popup-product"
      v-if="searchVisible"
      :isShowSearch="false"
      :hasPagination="false"
      :isCheck="true"
      :isCheckMemory="true"
      :searchVisible.sync="searchVisible"
      :addForm="addForm"
      :childSearch="loadProduct"
      @submit-call-back="selectProductList"
      :init-checks="initCheckList"
    ></portal-add-popup>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import EditLanguage from './components/edit-language.vue';
import EditStatus from './components/edit-status.vue';
import EditMsisdn from './components/edit-msisdn.vue';
import EditOperatorId from './components/edit-operatorId.vue';
import AuditLog from '@/views/identifier-center/pages/organization-operator/detail/audit-log/index.vue';
import PortalAddPopup from '@/components/portal-add-popup/index';
import { getDetaliTillRules } from '@/views/identifier-center/validator/till.js';
import {
  queryTillProductList,
  editProduct,
  queryTillDetails,
  getLanguages,
} from '@/views/identifier-center/api/api-till';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
export default {
  name: 'TillView',
  components: { TablePage, EditLanguage, EditStatus, EditMsisdn, EditOperatorId, PortalAddPopup, AuditLog },
  data() {
    return {
      editProductLogding: false,
      tillAudioLog: {
        title: this.$t('IDC.auditlog.AuditLogTitle'),
        desc: this.$t('IDC.auditlog.tillDesc'),
      },
      tabData: 'first',
      activeName: 'info',
      submitProductList: [],
      initCheckList: [],
      ishandleClick: false,
      productDataForm: {
        remark: '',
      },
      searchVisible: false,
      addForm: {},
      that: this,
      tillId: '',
      devices: [],
      labelData: '',
      msisdn: '',
      showTillProducts: [],
      isShowProduct: true,
      tillFormRules: getDetaliTillRules(this),
      languagesList: [],
      columns: [
        { label: this.$t('IDC.till.msisdn'), prop: 'msisdn', align: 'left', __slotName: 'msisdn' },
        { label: this.$t('IDC.till.iccid'), prop: 'iccid', align: 'left' },
        { label: this.$t('IDC.till.simSize'), prop: 'simSize', align: 'left' },
        {
          label: this.$t('common.label.status'),
          prop: 'status',
          align: 'left',
          __slotName: 'status',
          'sort-method': (a, b) => {
            return a.jobStatus > b.jobStatus ? 1 : -1;
          },
        },
      ],
      historyDevice: [
        { label: this.$t('IDC.till.msisdn'), prop: 'msisdn', align: 'left' },
        { label: this.$t('IDC.till.iccid'), prop: 'iccid', align: 'left' },
        { label: this.$t('IDC.till.simSize'), prop: 'simSize', align: 'left' },
        {
          label: this.$t('common.label.status'),
          prop: 'status',
          align: 'left',
          __slotName: 'status',

          'sort-method': (a, b) => {
            return a.jobStatus > b.jobStatus ? 1 : -1;
          },
        },
        {
          'sort-method': (a, b) => {
            return a.recycleTime > b.recycleTime ? 1 : -1;
          },
          label: this.$t('IDC.till.time'),
          prop: 'lastUpdateTime',
          align: 'left',
          __slotName: 'lastUpdateTime',
        },
      ],
      productColumns: [
        {
          width: '559',
          label: this.$t('IDC.till.createProductName'),
          prop: 'productName',
          align: 'left',

          'sort-method': (a, b) => {
            return a.productName > b.productName ? 1 : -1;
          },
        },
        {
          label: this.$t('common.label.description'),
          prop: 'description',
          align: 'left',

          'sort-method': (a, b) => {
            return a.description > b.description ? 1 : -1;
          },
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {
    this.getLanguagesList();
  },
  mounted() {
    let params = decodeURIComponent(this.$route.query.data);
    params = JSON.parse(params);
    this.tillId = params.tillId;
    this.getTillDetail();
  },

  methods: {
    async getLanguagesList() {
      let params = {
        dataId: 'CACHE_LANGUAGE',
        groupId: 'AG',
      };
      let res = await getLanguages(params);
      if (res.header && res.header.success) {
        this.languagesList = res.list;
      }
    },
    formatLanguage(val) {
      let languageName = '-';
      this.languagesList.forEach((item) => {
        if (val === item.languageCode) {
          languageName = item.languageName;
        }
      });
      return languageName;
    },
    handleEditLanguage(val) {
      this.$refs['editLanguage'].showView(val);
    },
    selectProductList(data) {
      let productList = [];
      this.submitProductList = [];
      this.initCheckList = data;
      if (data && data.length) {
        data.forEach((item) => {
          productList.push({
            productName: item.productName,
            description: item.description,
            productId: item.productIndex,
          });
          this.submitProductList.push(item.productIndex);
        });
      }
      this.showTillProducts = JSON.parse(JSON.stringify(productList));
    },
    changeProduct() {
      this.$refs['productForm'].validate((valid) => {
        if (valid) {
          this.submitLoading = true;
          let params = {
            receiverIdentityId: this.labelData.identityId,
            remark: this.productDataForm.remark,
            productIdList: this.submitProductList,
          };
          this.editProductLogding = true;
          editProduct(params)
            .then((res) => {
              this.submitLoading = false;
              if (res.header && res.header.success) {
                this.submitProductList = [];
                this.productDataForm.remark = '';
                this.productColumns.pop();
                this.$modal.msgSuccess(this.$t('IDC.till.editProductY'));
                this.isShowProduct = true;
                this.$refs.productForm.resetFields();
                this.labelData.tillProducts = JSON.parse(JSON.stringify(this.showTillProducts));
              } else {
                this.cancel();
              }
            })
            .finally(() => {
              this.editProductLogding = false;
            });
        }
      });
    },
    handleClick(val) {
      if (val.index === '0' && this.ishandleClick) {
        this.ishandleClick = false;
        if (!this.isShowProduct) {
          this.cancel();
        }
      } else if (val.index === '1') {
        this.ishandleClick = true;
      }
    },
    tabChange(val) {
      if (val.index === '1') {
        this.tabData = 'first';
      }
    },
    handleEditStatus(val) {
      this.$refs['editStatus'].showView(val);
    },
    handleEditMsisdn(val) {
      this.$refs['editMsisdn'].showView(val);
    },
    cancel() {
      this.initCheckList = [];
      this.productDataForm.remark = '';
      this.productColumns.pop();
      this.isShowProduct = true;
      let tillProducts = this.labelData.tillProducts;
      if (tillProducts && tillProducts.length > 0) {
        this.showTillProducts = JSON.parse(JSON.stringify(this.labelData.tillProducts));
        this.showTillProducts.forEach((item) => {
          this.initCheckList.push({
            productIndex: item.productId * 1,
          });
        });
      } else {
        this.showTillProducts = [];
        this.initCheckList = [];
      }
    },
    addProduct() {
      this.searchVisible = true;
      this.addForm = {
        title: this.$t('IDC.till.addProduct'),
        label: '',
        idKey: 'productIndex',
        key: 'productIndex',
        columns: [
          { label: this.$t('IDC.till.productName'), prop: 'productName', align: 'left' },
          { label: this.$t('IDC.till.description'), prop: 'description', align: 'left' },
        ],
      };
    },
    showProduct() {
      let labelData = this.labelData;
      if (labelData.status === '06') {
        let message = `The Till-${this.labelData.tillNumber} is in the Closed state which does not support the current service.`;
        this.$confirm(message, {
          confirmButtonText: this.$t('common.button.confirm'),
          showCancelButton: false,
          closeOnClickModal: false,
          type: 'warning',
        });
        return;
      }
      this.isShowProduct = false;
      this.productColumns.push({
        width: '200',
        label: this.$t('common.label.operation'),
        prop: 'operation',
        align: 'left',
        __slotName: 'operation',
      });
    },
    deleteTill(index) {
      let tipsInfo = this.$t('IDC.till.deleteProduct');
      let tipsTitle = this.$t('common.button.delete');
      this.$confirm(tipsInfo, tipsTitle, {
        confirmButtonText: this.$t('common.button.delete'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        customClass: 'delete-confirm',
        type: 'warning',
      }).then(() => {
        this.showTillProducts.splice(index, 1);
        this.submitProductList.splice(index, 1);
        this.initCheckList.splice(index, 1);
      });
    },
    handleEditDefaultOperator(val) {
      this.$refs['editDefaultOperator'].showView(val);
    },
    getTillDetail(val) {
      let params = {
        tillId: this.tillId,
      };
      queryTillDetails(params).then((res) => {
        if (res.header && res.header.success) {
          this.closeDialog(val);
          this.labelData = JSON.parse(JSON.stringify(res));
          this.showTillProducts = JSON.parse(JSON.stringify(res.tillProducts || []));
          let submitProductList = [];
          if (this.showTillProducts && this.showTillProducts.length > 0) {
            this.showTillProducts.forEach((item) => {
              submitProductList.push(item.productId);
              this.initCheckList.push({
                productIndex: item.productId * 1,
                productName: item.productName || '',
              });
            });
          }
          this.submitProductList = submitProductList;
        }
      });
    },
    closeDialog(val = '') {
      if (val == '') {
        return;
      } else if (val.type == 'language') {
        this.labelData.languageCode = val.value;
      } else if (val.type === 'msisdn') {
        this.getTillDetail();
      } else if (val.type === 'operatorId') {
        if (val.value.value === '') {
          this.$set(this.labelData, 'defaultOperatorIdentifier', '');
          this.$set(this.labelData, 'defaultOperatorId', '');
        } else {
          this.$set(this.labelData, 'defaultOperatorIdentifier', val.value.label);
          this.$set(this.labelData, 'defaultOperatorId', val.value.value);
        }
      } else if (val.type === 'status') {
        this.labelData.status = val.value;
      }
    },
    async loadProduct() {
      let params = {
        permCode: 'MANAGE_TILL_CHANGE_SERVICE',
        dataPermCode: 'TILL_PRODUCT',
      };
      let res = await queryTillProductList(params);
      return res;
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
  },
};
</script>

